<script setup>
import { defineProps } from "vue";

if (localStorage.contests) {
    var savedContests = localStorage.getItem('contests').split(',') 
    savedContests.forEach(element => {
      var checkbox = document.querySelector("input[value='" + element + "']");
      if (checkbox) { checkbox.checked = true }      
    });
}

const props = defineProps({
  contestData: Object,
  electionName: String,
  idx: Number,
});

function selectContest(contestValue) {
    if (localStorage.contests) {
        var saveContests = localStorage.getItem('contests').split(',') 
    } else {
        saveContests = []
    }

    var checkbox = document.querySelector("input[value='" + contestValue + "']");
    if (checkbox.checked) { checkbox.checked = false
    
        var index = saveContests.indexOf(contestValue);
        saveContests.splice(index, 1);
        localStorage.contests = saveContests

    } else { checkbox.checked = true

        saveContests.push(contestValue)
        localStorage.contests = saveContests
    }
}

</script>

<template>
  <div class="accordion accordion-flush mt-1" id="accordionFlushExample">
    <div class="accordion-item">
      <h2 class="accordion-header" id="flush-headingOne">
        <button class="accordion-button collapsed" type="button" data-bs-toggle="collapse" :data-bs-target="'#collapse-' + idx">
          {{ props.electionName }}
        </button>
      </h2>
      <div :id="'collapse-' + idx" class="accordion-collapse collapse" data-bs-parent="#accordionFlushExample">
        <table class="table table-hover">
          <tbody>
            <tr v-for="(contest, index) in props.contestData" :key="index" @click="selectContest(contest.contest_num)">
              <td class="clickable checkbox-col">
                <div class="form-check">
                  <input class="form-check-input" name="contest-list" type="checkbox" :value="contest.contest_num" id="CheckBox"/>
                </div>
              </td>
              <td class="clickable col-12">{{ contest.contest_name }}</td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>

<style scoped>
.accordion-button {
  background-color: rgba(0, 0, 0, 0.03);
  border-bottom: 1px solid rgba(0, 0, 0, 0.125);
}

.checkbox-col {
  width: 4%;
  padding-left: 2%;
}

</style>
